<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}">
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <div class="fixed-box">
      <div class="search">
        <van-search
          v-model="value"
          shape="round"
          background="#1B213B"
          placeholder="请输入标题"
        />
      </div>
      <div class="gg-tab">
        <span :class="spanActive == 0?'gg-tab-active':''" @click="butSpan(0)">行业资讯</span>
        <span :class="spanActive == 1?'gg-tab-active':''"  @click="butSpan(1)">财经资讯</span>
        <span :class="spanActive == 2?'gg-tab-active':''" @click="butSpan(2)">国际资讯</span>
        <span :class="spanActive == 3?'gg-tab-active':''" @click="butSpan(3)">系统资讯</span>
      </div>
      <div class="dividing-line"></div>
      </div>
      <div class="zhanwei"></div>
      <!--  -->
      <div class="list-item" v-for="(item,index) in newlists" :key="index" @click="toDetails(item.id)">
        <p class="title"  >{{item.title}}</p>
        <div class="ckxq">
          <span class="time-box" style="text-align: left;">{{format(item.create_time)}}</span>
          <span style="margin-left: 2.5rem;margin-right: 0.17rem" >查看详情</span>
          <van-icon name="arrow" style="vertical-align: middle"/>
        </div>
      </div>
      <!-- <div class="list-item" v-for="(item,index) in newlists" :key="index">
        <p class="title" @click="toDetails()" >{{item.title}}</p>
        <span class="time-box">{{format(item.create_time)}}</span>
        <div class="avator-box">
          <span class="left-icon">张</span>
          <span class="left-text">发布人：</span>
          <span>张某某</span>
          <div class="ckxq">
            <span style="margin-right: 0.17rem" @click="toDetails()">查看详情</span>
            <van-icon name="arrow" style="vertical-align: middle"/>
          </div>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { newlist } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { Search, Icon } from 'vant'
import NavHead from '@/components/head.vue'
Vue.use(Icon).use(Search)
export default {
  components: { NavHead },
  data () {
    return {
      title: '公告通知',
      value: '',
      newlistData: {
        type: 'news'
      },
      newlists: [], // 渲染内容
      list0: [], // 行业资讯
      list1: [], // 财经资讯
      list2: [], // 国际资讯
      list3: [], // 系统资讯
      curHeight: '',
      spanActive: 0
    }
  },
  created () {
    this.nesListFun()
    this.beforeMount()
  },
  methods: {
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d) + '  ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s)
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    nesListFun () {
      var that = this
      var data = objKeySort(this.newlistData)
      var token = sgin(data)
      this.newlistData.token = token
      newlist(this.newlistData).then(res => {
        console.log(res, that, '新闻列表')
        if (res.code === 0) {
          for (const [i, v] of res.data.entries()) {
            // '行业资讯','财经资讯','国际资讯','系统资讯'
            if (v.type === '行业资讯') {
              console.log(i)
              that.list0.push(v)
            } else if (v.type === '财经资讯') {
              that.list1.push(v)
            } else if (v.type === '国际资讯') {
              that.list2.push(v)
            } else if (v.type === '系统资讯') {
              that.list3.push(v)
            }
          }
          that.newlists = that.list0
          console.log(that.newlists, '00')
          console.log(that.list0, '0')
          console.log(that.list1, '2')
          console.log(that.list2, '2')
          console.log(that.list3, '3')
        }
      }).catch((err) => {
        console.log(err, that)
        delete this.newlistData.token
      })
    },
    toDetails (id) {
      this.$router.push({
        path: '/notice/detail',
        query: {
          news_id: id
        }
      })
    },
    butSpan (type) {
      var that = this
      if (type === 0) {
        that.newlists = that.list0
      } else if (type === 1) {
        that.newlists = that.list1
      } else if (type === 2) {
        that.newlists = that.list2
      } else if (type === 3) {
        that.newlists = that.list3
      }
      that.spanActive = type
    }
  }
}
</script>
